<template>
  <div
    class="paper"
    ref="question">
    <!-- 首次欢迎语 -->
    <div
      class="qlist paper-welcome">
      <img
        class="qicon"
        :src="`${baseUrl}questionPaper/qpIcon.png`"
        alt="">
      <div>
        Hi！欢迎加入微淼《小白理财训练营》，我是你理财路上的好朋友“小淼”，微淼已经帮助近千万学员普及财商知识。让我们互相了解一下吧，我会根据你的情况帮你<span style="color: #1e66ed;">【量身定制】</span>专属课程！
      </div>
    </div>
    <div
      class="questions"
      ref="list"
      v-if="index !== -1">
      <!-- 正常问题区域 -->
      <TransitionGroup
        name="fade"
        mode="out-in"
        tag="div"
        v-for="(item, key) in qList"
        :key="item.id">
        <div
          class="qlist"
          v-if="index >= key"
          :key="item.id"
          :ref="item.id">
          <img
            class="qicon"
            :src="`${baseUrl}questionPaper/qpIcon.png`"
            alt="">
          <div class="qstem"> {{ key+1 }}、{{ item.question }}</div>
          <VanRadioGroup
            v-model="item.answer"
            @change="scrollNext(key, item.id)"
            v-if="item.style===1">
            <VanRadio
              v-for="(v, i) in item.answer_list"
              :name="v.key"
              :key="i">
              <div
                :class="[
                  item.answer === v.key ? 'active' : '', 'opts']">
                {{ v.key }}、{{ v.desc }}
              </div>
            </VanRadio>
          </VanRadioGroup>
          <div v-else>
            <VanDatetimePicker
              v-model="uBornDay"
              :title="item.answer || '请滑动选择' "
              type="year-month"
              @confirm="userSelTime(key, ...arguments)"
              :min-date="minDate"
              :max-date="maxDate"
              :formatter="formatter" />
          </div>
        </div>
      </TransitionGroup>
      <!-- 最后一道题(结束语) -->
      <TransitionGroup v-if="index === qList.length">
        <div
          key="rear"
          class="qlist paper-welcome">
          <img
            class="qicon"
            :src="`${baseUrl}questionPaper/qpIcon.png`"
            alt="">
          <div>正在为您匹配专属老师，请不要走开哦…</div>
        </div>
      </TransitionGroup>
      <p
        class="item"
        ref="bottom"></p>
    </div>
    <img
      class="bgicon"
      :src="`${baseUrl}questionPaper/bg.png`"
      alt="">
    <div v-if="index > -1" class="progress">
      <div
        v-for="(v, k) in qList"
        :key="k"
        :class="[
          v.answer ? 'stepsactive' : '', 'steps']"></div>
    </div>
    <VanDialog
      class="dialog"
      v-model="showDialog"
      :show-confirm-button="false"
      :close-on-click-overlay="true">
      <img :src="`${baseUrl}questionPaper/dialog_bg1.png`" />
      <div class="dialog-content">
        小淼根据您的情况帮您【量身定制】专属课程，确定跳过么？
        <div class="dialog-sure btn" @click="goOnPage">继续问卷</div>
        <div class="dialog-cancel" @click="goQrcodePage">跳过问卷</div>
      </div>
    </VanDialog>
  </div>
</template>

<script>
import { getSearch } from '@/utils'
import Store from '@/store'
import wxAuth from '@/helper/Auth/wxAuth'
import preventBack from '@/mixins/preventBack'

export default {
  mixins: [preventBack],
  data () {
    return {
      paperList: { A: 44, B: 45, C: 46, D: 47 },
      abGroup: JSON.parse(localStorage.getItem('abGroup')),
      minDate: new Date(1920, 1, 1),
      maxDate: new Date(2021, 1, 1),
      uBornDay: new Date(1992, 1, 1),
      qList: [], // 问题列表
      index: null, // 当前进行到哪一个问题
      questionnaire_no: Number(getSearch('pageid')),
      showDialog: false,
      heartbeatTimes: 12 // 心跳次数
    }
  },
  async beforeRouteEnter (to, from, next) {
    if (from.name === 'Qrcode') {
      next({ name: 'CheckPhone', replace: true })
    }
    // 获取微信授权
    await wxAuth({ getTokenQuery: getSearch() })
    const { id } = Store.state.user.userInfo || {}
    // 获取分组
    const abGroup = await Store.dispatch('ABTest/getAbData', { domain: 'mini_program', abKey: 'question_test', whiteParam: id })
    if (!abGroup.ab_group) {
      next({ name: 'Qrcode', replace: true })
    }
    localStorage.setItem('abGroup', JSON.stringify(abGroup))
    next((vm) => {
      const updateLog = {
        alternate: JSON.stringify({
          mid: id,
          questionnaire_no: vm.paperList[abGroup.group]
        })
      }
      Store.$log.update(updateLog)
    })
  },
  watch: {
    index: {
      handler (v) {
        // 答完题目之后跳转到二维码页面（提测后打开）
        if (v === this.qList.length) {
          setTimeout(() => {
            this.$router.replace({
              name: 'Qrcode'
            })
          }, 3 * 1000)
        }
      },
      immediately: true
    }
  },
  created () {
    // 获取问题
    this.getQList()
    this.$log.show({
      current_page: location.href,
      module_ori: '基于页面',
      module_part: '106_tf_h5_00050'
    })
  },
  mounted () {
    this.setHeartBeat()
    const html = document.querySelector('html')
    html.style.background = '#f9f9f9'
    this.$on('userBack', () => {
      this.showDialog = true
      // 弹窗展示
      this.$log.show({
        current_page: location.href,
        module_ori: '挽留弹窗显示',
        module_part: '106_tf_h5_00053'
      })
    })
  },
  beforeDestroy () {
    this.heartbeat.stop()
  },
  methods: {
    // 继续答题
    goOnPage () {
      this.showDialog = false
      this.$log.click({
        current_page: location.href,
        module_ori: '提交选项',
        module_part: '106_tf_h5_00054'
      })
    },
    // 跳过答题（不答了）
    async goQrcodePage () {
      this.$log.click({
        current_page: location.href,
        module_ori: '跳过选项',
        module_part: '106_tf_h5_00073'
      })
      try {
        await this.$apis.question.postAnswer({
          questionnaire_no: this.questionnaire_no || this.paperList[this.abGroup.ab_group],
          skip_status: 2
        })
        // 跳过问卷之后跳转到二维码页面（提测后打开）
        this.$router.replace({
          name: 'Qrcode'
        })
      } catch (e) {
        console.log(e)
      }
    },
    // 年龄问题，用户点击确定按钮
    userSelTime (key, time) {
      if (this.qList[key].answer) {
        this.$toast('您已选过出生年月，无法修改')
        return
      }
      this.qList[key].answer = new Date(time).getFullYear().toString()
      this.scrollNext(key, this.qList[key].id)
    },
    // 年份格式化
    formatter (type, val) {
      if (type === 'year') {
        return `${val}年`
      }
      return val
    },
    // 提交答案，滚动下一题
    async scrollNext (i, id) {
      // 提交答案，报错终止
      if (!await this.submitAnswer(id)) return
      // 返回修改答案 || 答到最后一道题
      if (this.index !== i || this.index === this.qList.length) return
      this.index++
      this.scrollTo()
    },
    // 提交答案
    async submitAnswer (id) {
      // 提交用户答题
      const parmas = {
        questionnaire_no: this.questionnaire_no || this.paperList[this.abGroup.ab_group],
        questionnaire_answer: this.qList.map((item) => {
          return {
            id: item.id,
            answer: item.answer
          }
        }).filter(v => v.id === id)
      }
      const data = await this.$apis.question.postAnswer(parmas)
      this.$log.click({
        current_page: location.href,
        module_ori: '提交问卷',
        module_part: '106_tf_h5_00051'
      })
      if (data[0].code !== 1000) {
        this.$toast(`${data[0].id}:${data[0].msg}`)
        return false
      }
      return true
    },
    // 获取问题
    async getQList () {
      try {
        const { question = [], answer = [], skip_status: skipStatus } = await this.$apis.question.getQuestionList({
          questionnaire_no: this.questionnaire_no || this.paperList[this.abGroup.ab_group],
          need_answer_list: true
        })
        // 之前答过题目，或者跳过：直接跳到二维码页
        if (answer.length || skipStatus === 2) {
          this.$router.replace({
            name: 'Qrcode'
          })
        }
        this.qList = question
        await this.$nextTick()
        // 显示欢迎语
        this.index = -1
        // 3s之后结束欢迎语
        setTimeout(() => {
          this.index = 0
          this.scrollTo()
        }, 3 * 1000)
      } catch (e) {
        this.$toast(e.msg)
      }
    },
    // 滚动到下一题的位置
    async scrollTo () {
      this.$log.show({
        current_page: location.href,
        module_ori: '基于页面',
        module_part: '106_tf_h5_00052'
      })
      await this.$nextTick()
      const qListDom = document.querySelectorAll('.qlist')
      const rect = qListDom[this.index].getBoundingClientRect()
      window.scrollBy({ top: rect.top + rect.height, behavior: 'instant' })
    },
    // 设置心跳
    setHeartBeat () {
      this.heartbeat = this.$log.heartbeat(
        {
          current_page: location.href,
          event: 'heartbeat',
          module_ori: '时长埋点',
          module_part: '106_tf_h5_00055'
        },
        5000,
        1
      )
      this.heartbeat.start()
      setTimeout(() => {
        this.heartbeat.stop()
      }, (this.heartbeatTimes - 1) * 5000)
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog {
  width: 580px;
  height: 690px;
  background: transparent;
  border-radius: 12px;

  img {
    width: 580px;
  }

  &-content {
    height: 383px;
    background: #fff;
    line-height: 56px;
    color: #333;
    font-size: 32px;
    font-weight: 400;
    text-align: center;
    padding: 32px;

    .btn {
      width: 516px;
      height: 88px;
      background: #1e66ed;
      border-radius: 54px;
      font-size: 32px;
      font-weight: 500;
      color: #fff;
      line-height: 88px;
      margin-top: 30px;
    }
  }

  &-cancel {
    font-size: 32px;
    font-weight: 400;
    color: #999;
    line-height: 54px;
    padding-top: 36px;
  }
}

.paper {
  position: relative;
  height: 800px;
  padding: 0 32px;

  &-welcome {
    font-size: 36px;
    font-weight: 400;
    color: #333;
    line-height: 64px;
  }

  .item {
    height: 800px;
  }

  .qlist {
    position: relative;
    width: 574px;
    box-sizing: border-box;
    padding: 40px;
    background: #fff;
    margin-top: 206px;
    border-radius: 0 24px 24px 24px;

    .qicon {
      width: 126px;
      height: 168px;
      position: absolute;
      top: -140px;
      left: 0;
    }

    .qstem {
      font-size: 36px;
      font-weight: 500;
      color: #333;
      line-height: 56px;
      margin-bottom: 40px;
    }

    /deep/ .van-icon {
      display: none;
    }

    /deep/ .van-radio__label {
      display: inline-block;
      box-sizing: border-box;
      width: 496px;
    }

    /deep/ .van-picker__cancel {
      opacity: 0;
    }

    .opts {
      background: #f9fafd;
      width: 494px;
      border-radius: 12px;
      font-size: 32px;
      font-weight: 400;
      color: #666;
      line-height: 88px;
      padding-left: 32px;
    }

    .active {
      background: #1e66ed;
      color: #fff;
    }

    .opts:first-child {
      margin-bottom: 32px;
    }
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
  }

  .fade-enter {
    opacity: 0;
    transform: translateY(50px);
  }

  .fade-leave-to {
    opacity: 0;
    transform: translateY(-50px);
  }

  .bg {
    width: 100%;
  }
}

/deep/ .van-picker__title {
  font-size: 40px;
  font-weight: 500;
  color: #1e66ed;
  line-height: 56px;
}

/deep/ .van-datetime-picker {
  .van-picker-column:nth-child(2) {
    display: none;
  }
}

.progress {
  position: fixed;
  display: flex;
  right: 0;
  top: 0;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}

.bgicon {
  position: fixed;
  z-index: -1;
  bottom: 0;
  right: 0;
  width: 399px;
  height: 944px;
}

.steps {
  flex: 1;
  max-width: 80px;
  height: 6px;
  background: #e7eaf0;
  border-radius: 3px;
}

.van-radio {
  width: 540px;
}

.stepsactive {
  background: #1e66ed;
}
</style>
